.app {
  background-color: #eaf0f1;
  display: flex;
  flex: 1;
  flex-direction: column;
  height: 100vh;
  overflow-y: hidden;
}
.header,
.footer {
  display: flex;
  height: 10%;
  flex-direction: row;
  align-items: center;
  background: #dae0e2;
}

.header {
  color: #444;
  font-size: 20px;
  font-weight: bold;
}

.header .title {
  color: #4b4747;
}

.header img {
  width: 35px;
  height: 30px;
}

.header .sub-title {
  color: #4b4747;
}

.header > div {
  display: flex;
  flex: 1;
  justify-content: center;
}
.body {
  flex: 8;
  width: 100%;
  height: 75%;

  flex-direction: row;
  align-items: center;
  overflow-y: scroll;
  background: #f1f3f3;
}
.body::-webkit-scrollbar {
  width: 5;
}

.footer {
  justify-content: space-between;
}
.footer p {
  margin: 20px;
}
.footer p span {
  font-size: 12px;
}

.colorsGroup {
  margin-top: 45px;
  margin-bottom: 45px;
  display: flex;
  flex-direction: column;
}

.colorsGroup .title {
  font-size: 25px;
  margin: 10px;
  font-weight: 500;
}

.colorsGrid {
  display: flex;
  flex: 1;
  flex-direction: row;
  flex-wrap: wrap;
}
.color {
  width: 20%;
  height: 220px;
  color: #fff;
  align-items: center;
  display: flex;
  justify-content: center;
  border-radius: 5px;
  margin-top: 5px;
  transition: transform 0.2s;
  cursor: pointer;
}

.color:hover {
  transform: scale(1.1);
}

.header .toggle {
  margin-left: 20px;
}
.header .toggle span {
  margin-right: 5px;
  margin-left: 5px;
  position: relative;
  bottom: 6px;
  color: #df4c33;
}
